@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@600;800&display=swap");
html {
  box-sizing: border-box;
  font-size: 15px;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

ul {
  list-style-type: none;
}

* {
  font-family: "Manrope", sans-serif;
}

body::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 50%;
  background-color: hsl(226deg, 100%, 87%);
  border-bottom-left-radius: 8rem;
  z-index: -10;
  opacity: 0.2;
}
@media (min-width: 576px) {
  body::before {
    height: 60%;
  }
}

.card {
  padding: 1rem;
  width: 80%;
  max-width: 425px;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 0.4rem;
  box-shadow: 0 0 1px 1px hsl(223deg, 50%, 87%);
}
@media (min-width: 576px) {
  .card {
    padding: 2rem;
  }
}
.card .card__header {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: hsl(225deg, 20%, 60%);
  font-size: 0.6rem;
}
@media (min-width: 576px) {
  .card .card__header {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    font-size: 0.7rem;
  }
}
.card .card__header .card__header__title {
  letter-spacing: 3px;
}
.card .card__header .card__header--range {
  appearance: none;
  appearance: none;
  margin: 1.5rem 0;
  width: 100%;
  height: 0.6rem;
  background-color: hsl(174deg, 86%, 45%);
  border-radius: 4rem;
  cursor: pointer;
}
@media (min-width: 576px) {
  .card .card__header .card__header--range {
    order: 3;
  }
}
.card .card__header .card__header--range::-webkit-slider-thumb {
  appearance: none;
  appearance: none;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: hsl(174deg, 86%, 45%);
  background-image: url("./../images/icon-slider.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.card .card__header .card__header--range::-moz-silider-thumb {
  appearance: none;
  appearance: none;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: hsl(174deg, 86%, 45%);
  background-image: url("./../images/icon-slider.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.card .card__header .card__header--range::-o-slider-thumb {
  appearance: none;
  appearance: none;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: hsl(174deg, 86%, 45%);
  background-image: url("./../images/icon-slider.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.card .card__header .card__header--range::slider-thumb {
  appearance: none;
  appearance: none;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: hsl(174deg, 86%, 45%);
  background-image: url("./../images/icon-slider.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.card .card__header .card__header__stat {
  display: flex;
  align-items: center;
}
.card .card__header .card__header__stat span {
  color: hsl(227deg, 35%, 25%);
  font-size: 2rem;
  font-weight: bold;
}

.card__plan {
  padding: 1.5rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  color: hsl(225deg, 20%, 60%);
  font-size: 0.7rem;
}
@media (min-width: 576px) {
  .card__plan {
    justify-content: center;
    font-size: 0.8rem;
  }
}
.card__plan::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.1rem;
  background-color: hsl(227deg, 35%, 25%);
  opacity: 0.2;
}
.card__plan .card__plan--button {
  height: 1.5rem;
  margin: 0 0.4rem;
  width: 3rem;
  border: none;
  border-radius: 1rem;
  background-color: hsl(223deg, 50%, 87%);
  cursor: pointer;
  position: relative;
}
.card__plan .card__plan--button span {
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 50%;
  height: 1rem;
  right: 4px;
  position: absolute;
  top: 0;
  width: 1rem;
  z-index: 10;
  transform: translate(0, 25%);
  transition: 0.3s;
}
.card__plan .card__plan--button .active--toggle {
  right: 26px;
}
.card__plan .active--btn {
  background-color: hsl(174deg, 77%, 80%);
}
.card__plan .card__plan__yaerly {
  position: relative;
}
.card__plan .card__plan__yaerly::after {
  content: "25% discount";
  padding: 0 0.3rem;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  right: -4.5rem;
  background-color: hsl(14deg, 92%, 95%);
  color: hsl(15deg, 100%, 70%);
  font-size: 0.5rem;
  border-radius: 1rem;
}
@media (max-width: 420px) {
  .card__plan .card__plan__yaerly::after {
    display: none;
  }
}
@media (min-width: 576px) {
  .card__plan .card__plan__yaerly::after {
    font-size: 0.6rem;
    right: -5rem;
  }
}

.card__props {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  color: hsl(225deg, 20%, 60%);
  font-size: 0.9rem;
}
@media (min-width: 576px) {
  .card__props {
    flex-direction: row;
    font-size: 1rem;
  }
}
.card__props .card__props__list img {
  width: auto;
  height: auto;
  margin-right: 0.5rem;
}
.card__props .card__props--button {
  margin-top: 1.5rem;
  padding: 0.5rem 1.5rem;
  font-size: 0.8rem;
  border: none;
  border-radius: 1rem;
  color: hsl(225deg, 20%, 60%);
  background-color: hsl(227deg, 35%, 25%);
  transition: 0.3s ease-in-out;
  cursor: pointer;
}
@media (min-width: 576px) {
  .card__props .card__props--button {
    margin: 0;
  }
}
.card__props .card__props--button:hover {
  color: hsl(0deg, 0%, 100%);
}

body {
  width: 100%;
  height: 100vh;
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  background-color: hsl(230deg, 100%, 99%);
}
@media (min-width: 576px) {
  body {
    padding: 0;
    justify-content: center;
  }
}

.header {
  padding: 4rem 0;
  background-image: url("../images/pattern-circles.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 576px) {
  .header {
    padding: 5rem 0;
  }
}
.header .header__title {
  color: hsl(227deg, 35%, 25%);
  text-align: center;
  font-size: 1.4rem;
}
@media (min-width: 576px) {
  .header .header__title {
    font-size: 2rem;
  }
}
.header .header__descripe {
  color: hsl(225deg, 20%, 60%);
  text-align: center;
  font-size: 0.8rem;
}
@media (min-width: 576px) {
  .header .header__descripe {
    font-size: 0.9rem;
  }
}

.attribution {
  margin-top: 1.5rem;
  text-align: center;
  color: hsl(225deg, 20%, 60%);
}
.attribution a {
  color: hsl(227deg, 35%, 25%);
}